import src from "./parrot.jpg";

const canvas = new fabric.Canvas("canvas", {
  backgroundColor: "#ccc",
  selection: true,
  selectionClor: "blue",
  selectionLineWidth: 2,
  width: 320,
  height: 320
});

fabric.Image.fromURL(src, imgEl => {
  imgEl.set({
    left: 0,
    top: (320 - 256) / 2,
    width: 320,
    height: 256,
    selectable: false,
    evented: false,
    lockMovementX: true,
    lockMovementY: true,
    lockRotation: true,
    lockScalingX: true,
    lockScalingY: true,
    lockUniScaling: true,
    hasControls: false,
    hasBorders: false
  });
  canvas.add(imgEl);
});

var CropZone = fabric.util.createClass(fabric.Rect, {
  _render: function(ctx) {
    this.callSuper("_render", ctx);

    var canvas = ctx.canvas;
    var dashWidth = 7;

    // Set original scale
    var flipX = this.flipX ? -1 : 1;
    var flipY = this.flipY ? -1 : 1;
    var scaleX = flipX / this.scaleX;
    var scaleY = flipY / this.scaleY;

    ctx.scale(scaleX, scaleY);

    // Overlay rendering
    ctx.fillStyle = "rgba(0, 0, 0, 0.5)";
    this._renderOverlay(ctx);

    // Set dashed borders
    if (ctx.setLineDash !== undefined) ctx.setLineDash([dashWidth, dashWidth]);
    else if (ctx.mozDash !== undefined) ctx.mozDash = [dashWidth, dashWidth];

    // First lines rendering with black
    ctx.strokeStyle = "rgba(0, 0, 0, 0.2)";
    this._renderBorders(ctx);
    this._renderGrid(ctx);

    // Re render lines in white
    ctx.lineDashOffset = dashWidth;
    ctx.strokeStyle = "rgba(255, 255, 255, 0.4)";
    this._renderBorders(ctx);
    this._renderGrid(ctx);

    // Reset scale
    ctx.scale(1 / scaleX, 1 / scaleY);
  },

  _renderOverlay: function(ctx) {
    //var canvas = ctx.canvas;

    //
    //    x0    x1        x2      x3
    // y0 +------------------------+
    //    |\\\\\\\\\\\\\\\\\\\\\\\\|
    //    |\\\\\\\\\\\\\\\\\\\\\\\\|
    // y1 +------+---------+-------+
    //    |\\\\\\|         |\\\\\\\|
    //    |\\\\\\|    0    |\\\\\\\|
    //    |\\\\\\|         |\\\\\\\|
    // y2 +------+---------+-------+
    //    |\\\\\\\\\\\\\\\\\\\\\\\\|
    //    |\\\\\\\\\\\\\\\\\\\\\\\\|
    // y3 +------------------------+
    //

    var x0 = Math.ceil(-this.getScaledWidth() / 2 - this.left);
    var x1 = Math.ceil(-this.getScaledWidth() / 2);
    var x2 = Math.ceil(this.getScaledWidth() / 2);
    var x3 = Math.ceil(
      this.getScaledWidth() / 2 +
        (canvas.width - this.getScaledWidth() - this.left)
    );

    var y0 = Math.ceil(-this.getScaledHeight() / 2 - this.top);
    var y1 = Math.ceil(-this.getScaledHeight() / 2);
    var y2 = Math.ceil(this.getScaledHeight() / 2);
    var y3 = Math.ceil(
      this.getScaledHeight() / 2 +
        (canvas.height - this.getScaledHeight() - this.top)
    );

    ctx.beginPath();

    // Draw outer rectangle.
    // Numbers are +/-1 so that overlay edges don't get blurry.
    ctx.moveTo(x0 - 1, y0 - 1);
    ctx.lineTo(x3 + 1, y0 - 1);
    ctx.lineTo(x3 + 1, y3 + 1);
    ctx.lineTo(x0 - 1, y3 - 1);
    ctx.lineTo(x0 - 1, y0 - 1);
    ctx.closePath();

    // Draw inner rectangle.
    ctx.moveTo(x1, y1);
    ctx.lineTo(x1, y2);
    ctx.lineTo(x2, y2);
    ctx.lineTo(x2, y1);
    ctx.lineTo(x1, y1);

    ctx.closePath();
    ctx.fill();
  },

  _renderBorders: function(ctx) {
    ctx.beginPath();
    ctx.moveTo(-this.getScaledWidth() / 2, -this.getScaledHeight() / 2); // upper left
    ctx.lineTo(this.getScaledWidth() / 2, -this.getScaledHeight() / 2); // upper right
    ctx.lineTo(this.getScaledWidth() / 2, this.getScaledHeight() / 2); // down right
    ctx.lineTo(-this.getScaledWidth() / 2, this.getScaledHeight() / 2); // down left
    ctx.lineTo(-this.getScaledWidth() / 2, -this.getScaledHeight() / 2); // upper left
    ctx.stroke();
  },

  _renderGrid: function(ctx) {
    // Vertical lines
    ctx.beginPath();
    ctx.moveTo(
      -this.getScaledWidth() / 2 + (1 / 3) * this.getScaledWidth(),
      -this.getScaledHeight() / 2
    );
    ctx.lineTo(
      -this.getScaledWidth() / 2 + (1 / 3) * this.getScaledWidth(),
      this.getScaledHeight() / 2
    );
    ctx.stroke();
    ctx.beginPath();
    ctx.moveTo(
      -this.getScaledWidth() / 2 + (2 / 3) * this.getScaledWidth(),
      -this.getScaledHeight() / 2
    );
    ctx.lineTo(
      -this.getScaledWidth() / 2 + (2 / 3) * this.getScaledWidth(),
      this.getScaledHeight() / 2
    );
    ctx.stroke();
    // Horizontal lines
    ctx.beginPath();
    ctx.moveTo(
      -this.getScaledWidth() / 2,
      -this.getScaledHeight() / 2 + (1 / 3) * this.getScaledHeight()
    );
    ctx.lineTo(
      this.getScaledWidth() / 2,
      -this.getScaledHeight() / 2 + (1 / 3) * this.getScaledHeight()
    );
    ctx.stroke();
    ctx.beginPath();
    ctx.moveTo(
      -this.getScaledWidth() / 2,
      -this.getScaledHeight() / 2 + (2 / 3) * this.getScaledHeight()
    );
    ctx.lineTo(
      this.getScaledWidth() / 2,
      -this.getScaledHeight() / 2 + (2 / 3) * this.getScaledHeight()
    );
    ctx.stroke();
  }
});

const cropZone = new CropZone({
  fill: "transparent",
  hasBorders: false,
  originX: "left",
  originY: "top",
  cornerColor: "#444",
  cornerSize: 8,
  transparentCorners: false,
  lockRotation: true,
  hasRotatingPoint: false,
  lockMovementX: true,
  width: 320,
  height: 200,
  left: 0,
  top: 30
});

canvas.add(cropZone);
